.mainSlot{
    max-width: 800px;
    max-height: 600px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    flex: 0 1 33.333%;
    justify-content: space-between;
}
.slotRow{
    max-width: 800px;
    max-height: 180px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
}
.slotCol{
    max-width: 220px;
    height: 180px;
    flex: 0 1 33.333%;
}
img{
    width: 200px;
    height: 180px;
    margin: 0 5%;
}
.numbers{
    max-width: 780px;
    max-height: 600px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}
.amount{
    border: 1px solid black ;
    width: 200px;
    height: 50px;
   font-size: 25px;
}
.spin{
    width: 150px;
    height: 50px;
    background-color: rgb(9, 1, 19);
    color: white;
    font-size: 25px;
    margin: 0px  auto;
}
button{
    margin: 0px  auto;
}
